<template>
  <article class="ContactWrapper Profil">
    <div class="Profil-aside">
      <affix class="Profil-card" relative-element-selector=".Profil-aside" :scroll-affix="true"
             :offset="{top: offset_top, bottom: 20}" :enabled="stickyEnabled">

        <div class="Profil-image" :style="{ 'background-image': 'url(' + item.image + ')' }" v-if="item.image"></div>
        <div class="Profil-content">
          <div class="Profil-tags" v-if="item.tags">
            <div v-for="tag in item.tags" class="Tag">{{ tag }}</div>
          </div>
          <div class="Profil-actions">
            <button class="Profil-action">
              <svg class="icon" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 200 180.2">
                  <path
                    d="M200 56.09a55.79 55.79 0 0 1-17.08 40.36l-77.24 78-5.68 5.73-5.68-5.73-78.75-79.51A56.14 56.14 0 0 1 55.83 0 55.59 55.59 0 0 1 100 21.75a55.79 55.79 0 0 1 100 34.32zm-22.71 34.66a47.94 47.94 0 1 0-77.24-53.15 47.9 47.9 0 1 0-78.7 51.77L100 168.8z"
                  />
              </svg>  

              Ajouter a ma selection
            </button>
            <button class="Profil-action">
              <svg class="icon" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 41.41">
                <path
                  d="M6 41.41V32H3a3 3 0 0 1-3-3V3a3 3 0 0 1 3-3h42a3 3 0 0 1 3 3v26a3 3 0 0 1-3 3H15.41zM3 2a1 1 0 0 0-1 1v26a1 1 0 0 0 1 1h5v6.59L14.59 30H45a1 1 0 0 0 1-1V3a1 1 0 0 0-1-1z"/>
                <circle cx="16" cy="16" r="2"/>
                <circle cx="32" cy="16" r="2"/>
                <circle cx="24" cy="16" r="2"/>
              </svg>

              Demande de contact
            </button>
            <button class="Profil-action">
              <svg class="icon" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 50 50">
                <circle cx="43" cy="21" r="2"/>
                <path d="M41 50H9V28h32zm-30-2h28V30H11zm29-32a1 1 0 0 1-1-1V2H11v13a1 1 0 0 1-2 0V0h32v15a1 1 0 0 1-1 1z"/>
                <path
                  d="M35 44H15a1 1 0 0 1 0-2h20a1 1 0 0 1 0 2zm12-3h-7a1 1 0 0 1 0-2h7a1 1 0 0 0 1-1V17a1 1 0 0 0-1-1H3a1 1 0 0 0-1 1v21a1 1 0 0 0 1 1h7a1 1 0 0 1 0 2H3a3 3 0 0 1-3-3V17a3 3 0 0 1 3-3h44a3 3 0 0 1 3 3v21a3 3 0 0 1-3 3zm-16-1H15a1 1 0 0 1 0-2h16a1 1 0 0 1 0 2zm4-4H15a1 1 0 0 1 0-2h20a1 1 0 0 1 0 2z"/>
              </svg>

              Imprimer une fiche
            </button>
          </div>
          <div class="Profil-more">
            <p class="Profil-stand"><span>Prsent sur le stand :</span> AK 005</p>
          </div>
        </div>
      </affix>
    </div>

    <div class="ContactWrapper-main Profil-body">
      <div class="Profil-col">
        <h2 class="Profil-nameBis">{{ item.firstName }} {{ item.lastName }}</h2>
        <p class="Profil-localisation"><span>{{ item.city }}, {{ item.country }}</span></p>
      </div>

      <p class="Profil-position" v-if="minimumOneArguments(item.position, item.company)">{{ item.position }}<span
        v-if="allArgumentsDefined(item.position, item.company)"> - </span>{{ item.company }}</p>

      <div class="Profil-userTypes">
        <user-type v-for="userType in item.userTypes" :name="userType"></user-type>
      </div>

      <div class="Profil-resume"
           v-html="item.resume">
      </div>
      <ContactComplementary :infos="infosObject"></ContactComplementary>

    </div>

  </article>
</template>
<script>

  import {} from '../assets/components/profil/profil.scss'
  import {} from '../assets/components/tag/tag.scss'

  import UserType from '../components/UserType.vue'
  import ContactComplementary from '../components/ContactComplementary.vue'
  import {Affix} from 'vue-affix';

  export default {
    name: 'ContactProfil',
    mixins: [require('./mixins/teaser').default],
    components: {
      UserType,
      ContactComplementary,
      Affix
    },
    props: {
      offset: {
        required: false,
        type: Number
      },
      isSticky: {
        required: false,
        type: Boolean
      }
    },
    data () {
      return {
        offset_top: this.offset ? this.offset : document.querySelector('header').clientHeight + 20,
        stickyEnabled: !!this.isSticky
      }
    },
    computed: {
      infosObject () {
        return {
          address: this.item.address,
          country: this.item.country,
          email: this.item.email,
          phone: this.item.phone,
          portable: this.item.portable,
          website: this.item.website,
          blog: this.item.blog,
          cv: this.item.cv
        }
      }
    },
    methods: {
      onResize: function () {
        if (this.isSticky) {
          window.innerWidth < 700 ? this.stickyEnabled = false : this.stickyEnabled = true
        }
      }
    },
    mounted () {
      window.addEventListener('resize', this.onResize)
    },
    beforeDestroy: function () {
      window.removeEventListener('resize', this.onResize)
    }
  }
</script>

